.advantages {
  background-color: $color-solitude;
  background-image: url("../img/content/advantages/advantages-background-desktop.jpg");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: 0 0;

  @media (min-resolution: $retina-dppx) {
    background-image: url("../img/content/advantages/advantages-background-desktop@2x.jpg");
  }

  @include vp-767 {
    background-image: url("../img/content/advantages/advantages-background-tablet.jpg");

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/content/advantages/advantages-background-tablet@2x.jpg");
    }
  }

  @include vp-319 {
    background-image: url("../img/content/advantages/advantages-background-mobile.jpg");

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/content/advantages/advantages-background-mobile@2x.jpg");
    }
  }
}

.advantages__wrapper {
  padding: 110px 103px 120px;

  @include vp-767 {
    padding: 90px 44px 100px;
  }

  @include vp-319 {
    padding: 57px 15px 64px;
  }
}

.advantages__title {
  margin-bottom: 48px;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  text-align: center;
  color: $color-tangaroa;

  @include vp-767 {
    margin-bottom: 49px;
    font-size: 26px;
    line-height: 39px;
  }

  @include vp-319 {
    margin-bottom: 30px;
    font-size: 24px;
    line-height: 36px;
  }
}

.advantages__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 40px;
  row-gap: 40px;

  @include vp-767 {
    column-gap: 30px;
    row-gap: 30px;
  }
}

.advantages__item {
  position: relative;
  width: 360px;
  min-height: 242px;
  padding: 82px 0 24px 30px;
  background-color: $color-alice-blue;

  @include vp-767 {
    width: 206px;
    min-height: 310px;
    padding: 71px 0 19px 20px;
  }

  @include vp-319 {
    width: 290px;
    min-height: 250px;
  }

  &--cruise-liner {
    .advantages__description {
      padding-right: 42px;

      @include vp-767 {
        padding-right: 32px;
      }

      @include vp-319 {
        padding-right: 15px;
      }
    }
  }

  &--cruise-liner::before {
    content: "";
    position: absolute;
    top: 30px;
    left: 30px;
    width: 27px;
    height: 32px;
    background-image: url("../img/svg/icon-liner-desktop.svg");

    @include vp-767 {
      top: 25px;
      left: 20px;
      width: 21px;
      height: 26px;
      background-image: url("../img/svg/icon-liner-tablet.svg");
    }
  }

  &--all-inclusive {
    .advantages__description {
      padding-right: 45px;

      @include vp-767 {
        padding-right: 24px;
      }

      @include vp-319 {
        padding-right: 47px;
      }
    }
  }

  &--all-inclusive::before {
    content: "";
    position: absolute;
    top: 35px;
    left: 30px;
    width: 38px;
    height: 21px;
    background-image: url("../img/svg/icon-tick-desktop.svg");

    @include vp-767 {
      top: 29px;
      left: 20px;
      width: 33px;
      height: 19px;
      background-image: url("../img/svg/icon-tick-tablet.svg");
    }
  }

  &--excursion-tours {
    .advantages__description {
      padding-right: 80px;

      @include vp-767 {
        padding-right: 19px;
      }

      @include vp-319 {
        padding-right: 37px;
      }
    }
  }

  &--excursion-tours::before {
    content: "";
    position: absolute;
    top: 38px;
    left: 30px;
    width: 42px;
    height: 15px;
    background-image: url("../img/svg/icon-ship-desktop.svg");

    @include vp-767 {
      top: 32px;
      left: 20px;
      width: 38px;
      height: 13px;
      background-image: url("../img/svg/icon-ship-tablet.svg");
    }
  }
}

.advantages__name {
  margin-bottom: 8px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: $color-tangaroa;

  @include vp-767 {
    margin-bottom: 9px;
    line-height: 25px;
  }

  @include vp-319 {
    padding-right: 80px;
  }
}

.advantages__description {
  font-size: 14px;
  line-height: 20px;
  color: $color-tangaroa;
}
